:root {
  --color-one: #333; /* background */
  --color-two: #000; /* Black */
  --color-three: #fff; /* white */
  --color-four: #0077b5; /* blue */
  --color-five: #caccce; /* lightGrey */
  --color-six: #86888a; /* dark Grey */
}

html {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Baumans", BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--color-five);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
