.button {
  background: var(--color-three);
  color: black;
  color: var(--color-two);
  transition: linear 0.42s all;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-decoration: none;
  border-radius: 20px;

  border: 0;
  outline: 0;
}

.button--rev {
  background: var(--color-two);
  color: var(--color-three);
}


.button--m-top {
  margin-top: 20px;
}
.button:hover {
  background: var(--color-five);
  color: white;
  transition: linear 0.42s all;
  cursor: pointer;
}
.button--rev:hover {
  background: white;
  color: var(--color-five);
}
.button--circle {
  border-radius: 100%;
  height: 80px;
  width: 80px;
}

.button--right {
  visibility: hidden;
  position: absolute;
  z-index: -3;
  box-shadow: 0 0 5px 0.2px black;
}

.button__submit {
  height: 50px;
  font-size: 2rem;
  width: 100%;
}
.button__submit:hover {
  background: black;
  color: white;
}
/* medium */
@media screen and (min-width: 950px) {
  .button--right {
    visibility: visible;
    position: fixed;
    right: 20px;
