.brand {
  width: 100px;
  position: absolute;
  left: 0;
  margin-left: 20px;
}
.header__main {
    position: -webkit-sticky;
    position: sticky;
}

.header__menu-content--link {
  display: -webkit-flex;
  display: flex;
  padding: 5px;
  color: black;
  font-size: 1.3rem;
  transition: color 0.35s linear;
  width: 200px;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 10px;
  height: 27px;
  text-decoration: none;
}
.header__menu-content--link:hover {
  color: #ddd;
  transition: color 0.35s linear;
}



.header__menu-icon {
  width: 20px;
  height: 19px;
  position: fixed;
  top: 35px;
  right: 30px;
  z-index: 10;
  overflow: hidden;
  cursor: pointer;
}

.header__menu-icon::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 3px;
  background-color: white;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  overflow: hidden;
}

.header__menu-icon__middle {
  position: absolute;
  top: 8px;
  left: 0;
  width: 20px;
  height: 3px;
  background-color: white;
  -webkit-transform-origin: 0 50%;
          transform-origin: 0 50%;
  transition: all 0.3s ease-out;
}

.header__menu-icon::after {
  content: "";
  position: absolute;
  bottom: 0px;
  left: 0;
  width: 20px;
  height: 3px;
  background-color: white;
  -webkit-transform-origin: 0 100%;
          transform-origin: 0 100%;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  overflow: hidden;
}


.header__menu-content {
  position: fixed;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 200px;
  list-style-type: none;
  margin: 0;
  padding: 0;
  opacity: 0;
  z-index: -10;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  transition: background 0.3s ease;
  list-style: none;
}
.header__menu-content--is-visible {
  overflow: hidden;
  width: 100%;
  opacity: 1;
  z-index: 5;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: rgba(255,255,255,0.6);
  transition: background 0.5s ease;
}
.header__menu-icon--close-x::before {
  -webkit-transform: rotate(45deg) scaleX(1.25);
          transform: rotate(45deg) scaleX(1.25);
}

.header__menu-icon--close-x::after {
  -webkit-transform: rotate(-45deg) scaleX(1.25) translateY(1px);
          transform: rotate(-45deg) scaleX(1.25) translateY(1px);
}
.header__menu-icon--close-x .header__menu-icon__middle {
  opacity: 0;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
}

/* medium */
